// stores/chat.js
import { defineStore } from 'pinia'

export const useChatStore = defineStore('chat', {
  state: () => ({
    messages: [],
  }),
  actions: {
    addMessage(message) {
      this.messages.push({
        ...message,
        id: Date.now(),
        timestamp: new Date().toISOString(),
      })
    },
  },
})
